const IndustryImpact = () => {
  // Existing state
  const [selectedRegion, setSelectedRegion] = useState(null);
  const [mapTooltip, setMapTooltip] = useState({ show: false, content: '', x: 0, y: 0 });
  const [mapError, setMapError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [showGame, setShowGame] = useState(false);
  const [score, setScore] = useState(0);
  const [selectedChallenge, setSelectedChallenge] = useState(null);
  const [selectedSolution, setSelectedSolution] = useState(null);
  const [sliderPositions, setSliderPositions] = useState({});
  const sliderRefs = useRef({});

  const handleSliderDrag = (industry, x) => {
    const container = sliderRefs.current[industry]?.getBoundingClientRect();
    if (!container) return;
    
    const newPosition = Math.max(0, Math.min(100, (x / container.width) * 100));
    setSliderPositions(prev => ({
      ...prev,
      [industry]: newPosition
    }));
  };

  const industryMarkers = [
    {
      id: 'finance',
      name: 'Finance',
      coordinates: [-0.1276, 51.5074], // London
      color: '#00ffe7',
      adoption: '89%',
      impact: 'High frequency trading & risk analysis'
    },
    {
      id: 'healthcare',
      name: 'Healthcare',
      coordinates: [-74.0059, 40.7128], // New York
      color: '#ff00c8',
      adoption: '94%',
      impact: 'Diagnostic AI & drug discovery'
    },
    {
      id: 'manufacturing',
      name: 'Manufacturing',
      coordinates: [121.4737, 31.2304], // Shanghai
      color: '#7b61ff',
      adoption: '87%',
      impact: 'Smart factories & automation'
    },
    {
      id: 'tech',
      name: 'Technology',
      coordinates: [-122.4194, 37.7749], // San Francisco
      color: '#00b3ff',
      adoption: '96%',
      impact: 'AI development & cloud computing'
    }
  ];

  const regions = [
    { name: 'North America', aiAdoption: 92, color: '#00ffe7' },
    { name: 'Europe', aiAdoption: 88, color: '#00b3ff' },
    { name: 'Asia Pacific', aiAdoption: 95, color: '#7b61ff' },
    { name: 'Middle East', aiAdoption: 65, color: '#ff9a00' },
    { name: 'Africa', aiAdoption: 45, color: '#ff00c8' },
    { name: 'South America', aiAdoption: 58, color: '#00ff88' }
  ];

  const handleMouseEnter = (geo) => {
    setSelectedRegion(geo);
  };

  const handleMouseLeave = () => {
    setSelectedRegion(null);
  };

  const handleMarkerClick = (marker, event) => {
    setMapTooltip({
      show: true,
      content: `${marker.name}: ${marker.adoption} AI Adoption\n${marker.impact}`,
      x: event.clientX,
      y: event.clientY
    });
  };

  const handleMarkerLeave = () => {
    setMapTooltip(prev => ({ ...prev, show: false }));
  };

  const getRegionColor = (geo) => {
    const region = regions.find(r => r.name === geo.properties.name);
    if (!region) return props => props.theme.colors.glass.primary;
    const intensity = region.aiAdoption / 100;
    return `rgba(0, 255, 231, ${intensity})`;
  };

  useEffect(() => {
    const loadMap = async () => {
      try {
        setIsLoading(true);
        // Simulate map data loading
        await new Promise(resolve => setTimeout(resolve, 1000));
        setIsLoading(false);
      } catch (error) {
        setMapError('Error loading map data. Please try again.');
        setIsLoading(false);
      }
    };
    loadMap();
  }, []);



  const handleChallengeSelect = (challenge) => {
    setSelectedChallenge(challenge);
    setSelectedSolution(null);
  };

  const handleSolutionSelect = (solution) => {
    setSelectedSolution(solution);
  };

  const checkMatch = () => {
    if (!selectedChallenge || !selectedSolution) return;

    if (selectedChallenge.solution === selectedSolution.id) {
      setScore(prev => prev + 1);
      // Reset selections after successful match
      setSelectedChallenge(null);
      setSelectedSolution(null);
    }
  };

  const resetGame = () => {
    setSelectedChallenge(null);
    setSelectedSolution(null);
    setScore(0);
  };

  return (
    <SectionWrapper>
      <EcosystemHeader>
        <Title
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
          🌍 AI Industry Ecosystem
        </Title>
        <EcosystemSubtitle
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
        >
          Explore how AI is transforming industries across the globe
        </EcosystemSubtitle>
      </EcosystemHeader>

      <MapContainer>
        <ComposableMap 
          projection="geoMercator"
          style={{
            width: "100%",
            height: "100%"
          }}
        >
          <ZoomableGroup center={[0, 30]} zoom={1}>
            <Geographies geography="/world-countries.json">
              {({ geographies }) =>
                geographies.map(geo => (
                  <Geography
                    key={geo.rsmKey}
                    geography={geo}
                    fill={getRegionColor(geo)}
                    stroke="#ffffff"
                    strokeWidth={0.5}
                    style={{
                      default: {
                        outline: "none"
                      },
                      hover: {
                        fill: "#00ffe7",
                        outline: "none",
                        transition: "all 0.3s"
                      }
                    }}
                    onMouseEnter={() => handleMouseEnter(geo)}
                    onMouseLeave={handleMouseLeave}
                  />
                ))
              }
            </Geographies>

            {industryMarkers.map(marker => (
              <Marker
                key={marker.id}
                coordinates={marker.coordinates}
              >
                <g
                  transform="translate(-12, -24)"
                  onClick={(e) => handleMarkerClick(marker, e)}
                  onMouseLeave={handleMarkerLeave}
                  style={{ cursor: 'pointer' }}
                >
                  <circle
                    r={8}
                    fill={marker.color}
                    stroke="#fff"
                    strokeWidth={2}
                    style={{
                      filter: `drop-shadow(0 0 8px ${marker.color})`
                    }}
                  />
                  <text
                    textAnchor="middle"
                    y={-10}
                    style={{ 
                      fill: "#fff", 
                      fontSize: "14px",
                      textShadow: "0 0 5px rgba(0,0,0,0.5)"
                    }}
                  >
                    {marker.name}
                  </text>
                </g>
              </Marker>
            ))}
          </ZoomableGroup>
        </ComposableMap>

        <AnimatePresence>
          {selectedRegion && (
            <IndustryStats
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              exit={{ opacity: 0, x: 20 }}
            >
              <h3>{selectedRegion.properties.name}</h3>
              <p>AI Adoption Rate: {regions.find(r => r.name === selectedRegion.properties.name)?.aiAdoption}%</p>
            </IndustryStats>
          )}
        </AnimatePresence>

        <AnimatePresence>
          {mapTooltip.show && (
            <MapTooltip
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{ opacity: 1, scale: 1 }}
              exit={{ opacity: 0, scale: 0.8 }}
              style={{
                left: mapTooltip.x,
                top: mapTooltip.y
              }}
            >
              {mapTooltip.content}
            </MapTooltip>
          )}
        </AnimatePresence>

        <AnimatePresence>
          {isLoading && (
            <LoadingOverlay
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
            >
              Loading map data...
            </LoadingOverlay>
          )}
        </AnimatePresence>

        {mapError && (
          <div style={{ color: 'red', textAlign: 'center', padding: '1rem' }}>
            {mapError}
          </div>
        )}
      </MapContainer>

      {/* Game Launch Button */}
      <PlayButton
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.95 }}
        onClick={() => setShowGame(true)}
      >
        🎮 Play Industry Transformation Game
      </PlayButton>

      {/* Game Modal */}
      <AnimatePresence>
        {showGame && (
          <GameModal
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <GameContainer
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              transition={{ type: "spring", duration: 0.5 }}
            >
              <GameHeader>
                <h2>Match AI Solutions to Industry Challenges</h2>
                <p>Select a challenge and match it with the correct AI solution</p>
              </GameHeader>

              <ScoreDisplay
                initial={{ opacity: 0, y: -20 }}
                animate={{ opacity: 1, y: 0 }}
              >
                Score: <span>{score}</span>
              </ScoreDisplay>

              <GameGrid>
                {industryData.map(industry => (
                  <GameCard
                    key={industry.name}
                    selected={selectedChallenge?.name === industry.name}
                    onClick={() => handleChallengeSelect(industry)}
                    whileHover={{ scale: 1.02 }}
                    whileTap={{ scale: 0.98 }}
                  >
                    <h3>{industry.name}</h3>
                    <p>{industry.beforeAfter.before}</p>
                  </GameCard>
                ))}
              </GameGrid>

              <GameControls>
                <GameButton
                  onClick={() => setShowGame(false)}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  Close Game
                </GameButton>

                <GameButton
                  onClick={checkMatch}
                  disabled={!selectedChallenge || !selectedSolution}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  Check Match
                </GameButton>

                <GameButton
                  onClick={resetGame}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  Reset Game
                </GameButton>
              </GameControls>
            </GameContainer>
          </GameModal>
        )}
      </AnimatePresence>

      <div style={{ marginTop: props => props.theme.spacing.xxl }}>
        {industryData.map(industry => (
          <IndustryCard
            key={industry.name}
            color={industry.color}
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
          >
            <h2>{industry.name}</h2>
            <p>AI Adoption Rate: {industry.adoption}%</p>
            
            <SliderContainer ref={el => sliderRefs.current[industry.name] = el}>
              <SliderContent>
                <SliderSide>{industry.beforeAfter.before}</SliderSide>
                <SliderSide>{industry.beforeAfter.after}</SliderSide>
              </SliderContent>
              <SliderHandle
                drag="x"
                dragConstraints={sliderRefs.current[industry.name]}
                dragElastic={0}
                dragMomentum={false}
                onDrag={(_, info) => handleSliderDrag(industry.name, info.point.x)}
                animate={{ x: `${sliderPositions[industry.name] || 50}%` }}
              />
            </SliderContainer>

            <StatsGrid>
              {Object.entries(industry.keyStats).map(([key, value]) => (
                <StatCard
                  key={key}
                  initial={{ opacity: 0, scale: 0.9 }}
                  whileInView={{ opacity: 1, scale: 1 }}
                  viewport={{ once: true }}
                  whileHover={{ scale: 1.05 }}
                >
                  <h3>{value}</h3>
                  <p>{key.replace(/([A-Z])/g, ' $1').trim()}</p>
                </StatCard>
              ))}
            </StatsGrid>

            <Timeline>
              {industry.timeline.map((item, index) => (
                <TimelineItem
                  key={item.year}
                  initial={{ opacity: 0, x: index % 2 === 0 ? -50 : 50 }}
                  whileInView={{ opacity: 1, x: 0 }}
                  viewport={{ once: true }}
                >
                  <div className="content">
                    <h3>{item.year}</h3>
                    <p>{item.event}</p>
                  </div>
                </TimelineItem>
              ))}
            </Timeline>
          </IndustryCard>
        ))}
      </div>
    </SectionWrapper>
  );
};

export default IndustryImpact;