import React, { useState, useEffect } from 'react';
import styled, { keyframes, css } from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';

const SectionWrapper = styled.div`
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
  min-height: 100vh;
`;

const TimeMachineHeader = styled.div`
  text-align: center;
  margin-bottom: 3rem;
`;

const timeTravel = keyframes`
  0% { transform: rotateY(0deg); }
  50% { transform: rotateY(180deg); }
  100% { transform: rotateY(360deg); }
`;

const Title = styled(motion.h1)`
  font-size: ${props => props.theme.typography.fontSize['5xl']};
  font-weight: ${props => props.theme.typography.fontWeight.bold};
  background: ${props => props.theme.colors.gradients.highlight};
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 1rem;
  
  @media (max-width: 768px) {
    font-size: ${props => props.theme.typography.fontSize['3xl']};
  }
`;

const TimeMachineSubtitle = styled(motion.p)`
  font-size: ${props => props.theme.typography.fontSize.xl};
  color: ${props => props.theme.colors.text.secondary};
  margin-bottom: 2rem;
`;

const TimeMachineInterface = styled.div`
  background: ${props => props.theme.colors.glass.primary};
  border: 3px solid ${props => props.theme.colors.accent};
  border-radius: 30px;
  padding: 3rem;
  margin-bottom: 3rem;
  backdrop-filter: blur(20px);
  position: relative;
  overflow: hidden;
  
  &::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(45deg, transparent 30%, rgba(0, 255, 231, 0.05) 50%, transparent 70%);
    ${css`animation: ${timeTravel} 10s linear infinite;`}
  }
`;

const TimeControls = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
`;

const TimeButton = styled(motion.button)`
  background: ${props => props.selected ? 
    props.theme.colors.gradients.accent : 
    props.theme.colors.glass.secondary
  };
  border: 2px solid ${props => props.selected ? 
    props.theme.colors.accent : 
    props.theme.colors.glass.border
  };
  border-radius: 25px;
  padding: 1rem 2rem;
  color: ${props => props.theme.colors.text.primary};
  font-weight: bold;
  cursor: pointer;
  backdrop-filter: blur(20px);
  position: relative;
  overflow: hidden;
  
  &:hover {
    border-color: ${props => props.theme.colors.accent};
    box-shadow: 0 0 20px ${props => props.theme.colors.accent};
  }
  
  ${props => props.selected && `
    box-shadow: 0 0 30px ${props.theme.colors.accent};
  `}
`;

const BalanceScaleContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 3rem;
`;

const ScaleSupport = styled.div`
  width: 20px;
  height: 200px;
  background: ${props => props.theme.colors.glass.border};
  margin-bottom: 2rem;
  border-radius: 10px;
  position: relative;
  
  &::before {
    content: '⚖️';
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 2rem;
  }
`;

const ScaleBeam = styled(motion.div)`
  width: 400px;
  height: 8px;
  background: ${props => props.theme.colors.accent};
  border-radius: 4px;
  position: relative;
  box-shadow: 0 0 20px ${props => props.theme.colors.accent};
  
  @media (max-width: 768px) {
    width: 300px;
  }
`;

const ScalePan = styled(motion.div)`
  position: absolute;
  width: 150px;
  height: 80px;
  background: ${props => props.theme.colors.glass.primary};
  border: 2px solid ${props => props.theme.colors.glass.border};
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(20px);
  
  ${props => props.side === 'left' && `
    left: -75px;
    top: 20px;
  `}
  
  ${props => props.side === 'right' && `
    right: -75px;
    top: 20px;
  `}
  
  @media (max-width: 768px) {
    width: 120px;
    height: 60px;
    
    ${props => props.side === 'left' && `
      left: -60px;
    `}
    
    ${props => props.side === 'right' && `
      right: -60px;
    `}
  }
`;

const PanIcon = styled.div`
  font-size: 2rem;
  margin-bottom: 0.5rem;
`;

const PanLabel = styled.div`
  color: ${props => props.theme.colors.text.primary};
  font-size: 0.8rem;
  font-weight: bold;
  text-align: center;
`;

const PanNumber = styled.div`
  color: ${props => props.theme.colors.accent};
  font-size: 1.2rem;
  font-weight: bold;
`;

const JobFlowVisualization = styled.div`
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  gap: 2rem;
  align-items: center;
  margin-bottom: 3rem;
  
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
`;

const JobCategory = styled(motion.div)`
  background: ${props => props.theme.colors.glass.primary};
  border: 2px solid ${props => props.type === 'displaced' ? 
    props.theme.colors.error : 
    props.theme.colors.neon
  };
  border-radius: 20px;
  padding: 2rem;
  text-align: center;
  backdrop-filter: blur(20px);
`;

const CategoryTitle = styled.h3`
  color: ${props => props.type === 'displaced' ? 
    props.theme.colors.error : 
    props.theme.colors.neon
  };
  font-size: 1.5rem;
  margin-bottom: 1rem;
`;

const JobList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
`;

const JobItem = styled(motion.div)`
  background: ${props => props.theme.colors.glass.secondary};
  border: 1px solid ${props => props.theme.colors.glass.border};
  border-radius: 10px;
  padding: 0.8rem;
  color: ${props => props.theme.colors.text.secondary};
  font-size: 0.9rem;
  
  &:hover {
    border-color: ${props => props.theme.colors.accent};
    color: ${props => props.theme.colors.text.primary};
    transform: translateX(5px);
  }
`;

const FlowArrows = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  
  @media (max-width: 768px) {
    transform: rotate(90deg);
  }
`;

const FlowArrow = styled(motion.div)`
  font-size: 2rem;
  color: ${props => props.theme.colors.accent};
  filter: drop-shadow(0 0 10px ${props => props.theme.colors.accent});
`;

const TimelineContainer = styled.div`
  background: ${props => props.theme.colors.glass.primary};
  border: 2px solid ${props => props.theme.colors.glass.border};
  border-radius: 20px;
  padding: 2rem;
  backdrop-filter: blur(20px);
  margin-bottom: 3rem;
`;

const TimelineTitle = styled.h3`
  color: ${props => props.theme.colors.text.primary};
  text-align: center;
  margin-bottom: 2rem;
  font-size: 1.5rem;
`;

const TimelineTrack = styled.div`
  position: relative;
  height: 100px;
  background: ${props => props.theme.colors.secondary};
  border-radius: 10px;
  margin-bottom: 2rem;
  overflow: hidden;
  border: 1px solid ${props => props.theme.colors.glass.border};
`;

const TimelineProgress = styled(motion.div)`
  height: 100%;
  background: ${props => props.theme.colors.gradients.accent};
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: white;
  box-shadow: 0 0 20px ${props => props.theme.colors.accent};
`;

const ScenarioCards = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
`;

const ScenarioCard = styled(motion.div)`
  background: ${props => props.theme.colors.glass.primary};
  border: 2px solid ${props => props.selected ? props.theme.colors.accent : props.theme.colors.glass.border};
  border-radius: 20px;
  padding: 2rem;
  cursor: pointer;
  backdrop-filter: blur(20px);
  
  ${props => props.selected && `
    box-shadow: 0 0 30px ${props.theme.colors.accent};
    transform: scale(1.02);
  `}
`;

const ScenarioIcon = styled.div`
  font-size: 3rem;
  text-align: center;
  margin-bottom: 1rem;
`;

const ScenarioTitle = styled.h4`
  color: ${props => props.theme.colors.text.primary};
  font-size: 1.2rem;
  margin-bottom: 1rem;
  text-align: center;
`;

const ScenarioDescription = styled.p`
  color: ${props => props.theme.colors.text.secondary};
  line-height: 1.5;
  font-size: 0.9rem;
`;

const JobDisplacement = () => {
  const [selectedYear, setSelectedYear] = useState(2025);
  const [selectedScenario, setSelectedScenario] = useState(null);
  const [scaleRotation, setScaleRotation] = useState(0);

  const timePoints = [
    { year: 2020, label: 'Pre-AI Era' },
    { year: 2025, label: 'Current State' },
    { year: 2030, label: 'AI Integration' },
    { year: 2035, label: 'AI Maturity' }
  ];

  const yearData = {
    2020: {
      displaced: ['Manual Data Entry', 'Basic Customer Service', 'Routine Assembly', 'Simple Bookkeeping'],
      created: ['Data Analyst', 'UX Designer', 'Cloud Engineer', 'Content Creator'],
      displacedCount: 15,
      createdCount: 12,
      balance: -3
    },
    2025: {
      displaced: ['Traditional IT Support', 'Basic Translation', 'Simple Writing Tasks', 'Routine Legal Research'],
      created: ['AI Engineer', 'Prompt Engineer', 'AI Ethics Specialist', 'Human-AI Interaction Designer'],
      displacedCount: 25,
      createdCount: 35,
      balance: 10
    },
    2030: {
      displaced: ['Basic Accounting', 'Simple Medical Diagnosis', 'Standard Teaching', 'Basic Programming'],
      created: ['AI System Designer', 'Robot Coordinator', 'AI Trainer', 'Synthetic Media Specialist'],
      displacedCount: 45,
      createdCount: 75,
      balance: 30
    },
    2035: {
      displaced: ['Complex Analysis Tasks', 'Advanced Customer Service', 'Specialized Manufacturing', 'Routine Management'],
      created: ['AI Philosopher', 'Quantum-AI Specialist', 'Consciousness Engineer', 'Reality Designer'],
      displacedCount: 65,
      createdCount: 120,
      balance: 55
    }
  };

  const scenarios = [
    {
      id: 'optimistic',
      icon: '🚀',
      title: 'Optimistic Scenario',
      description: 'Rapid AI adoption creates 3 new jobs for every 1 displaced. Massive investment in retraining programs ensures smooth transition.'
    },
    {
      id: 'realistic',
      icon: '⚖️',
      title: 'Realistic Scenario',
      description: 'Gradual transition with 1.5 new jobs created for every 1 displaced. Some friction but manageable with proper policy support.'
    },
    {
      id: 'pessimistic',
      icon: '⚠️',
      title: 'Pessimistic Scenario',
      description: 'Slower job creation with significant displacement. Only 0.8 new jobs for every 1 displaced, requiring major intervention.'
    }
  ];

  const currentData = yearData[selectedYear];

  useEffect(() => {
    const newRotation = Math.max(-15, Math.min(15, currentData.balance * 0.5));
    setScaleRotation(newRotation);
  }, [selectedYear, currentData.balance]);

  return (
    <SectionWrapper>
      <TimeMachineHeader>
        <Title
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
          ⏰ AI Job Time Machine
        </Title>
        <TimeMachineSubtitle
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
        >
          Travel through time to see how AI reshapes the job market
        </TimeMachineSubtitle>
      </TimeMachineHeader>

      <TimeMachineInterface>
        <TimeControls>
          {timePoints.map((point, index) => (
            <TimeButton
              key={point.year}
              selected={selectedYear === point.year}
              onClick={() => setSelectedYear(point.year)}
              initial={{ opacity: 0, x: -50 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: 0.1 * index }}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              🕒 {point.year} - {point.label}
            </TimeButton>
          ))}
        </TimeControls>

        <BalanceScaleContainer>
          <ScaleSupport />
          <ScaleBeam
            animate={{ 
              rotate: scaleRotation
            }}
            transition={{ duration: 1, ease: "easeInOut" }}
          >
            <ScalePan 
              side="left"
              animate={{
                y: -scaleRotation * 2
              }}
              transition={{ duration: 1, ease: "easeInOut" }}
            >
              <PanIcon>📉</PanIcon>
              <PanLabel>Jobs Displaced</PanLabel>
              <PanNumber>{currentData.displacedCount}M</PanNumber>
            </ScalePan>
            
            <ScalePan 
              side="right"
              animate={{
                y: scaleRotation * 2
              }}
              transition={{ duration: 1, ease: "easeInOut" }}
            >
              <PanIcon>📈</PanIcon>
              <PanLabel>Jobs Created</PanLabel>
              <PanNumber>{currentData.createdCount}M</PanNumber>
            </ScalePan>
          </ScaleBeam>
          
          <motion.div
            style={{
              marginTop: '2rem',
              fontSize: '1.2rem',
              fontWeight: 'bold',
              color: currentData.balance >= 0 ? '#10b981' : '#ef4444'
            }}
            key={selectedYear}
            initial={{ opacity: 0, scale: 0.8 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.5 }}
          >
            Net Change: {currentData.balance >= 0 ? '+' : ''}{currentData.balance}M Jobs
          </motion.div>
        </BalanceScaleContainer>

        <JobFlowVisualization>
          <JobCategory type="displaced">
            <CategoryTitle type="displaced">📉 Jobs Being Displaced</CategoryTitle>
            <JobList>
              {currentData.displaced.map((job, index) => (
                <JobItem
                  key={job}
                  initial={{ opacity: 0, x: -20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ delay: 0.1 * index }}
                  whileHover={{ scale: 1.02 }}
                >
                  {job}
                </JobItem>
              ))}
            </JobList>
          </JobCategory>

          <FlowArrows>
            <FlowArrow
              animate={{ 
                x: [0, 10, 0],
                scale: [1, 1.2, 1]
              }}
              transition={{ duration: 2, repeat: Infinity }}
            >
              ➡️
            </FlowArrow>
            <FlowArrow
              animate={{ 
                rotate: [0, 360],
                scale: [1, 1.1, 1]
              }}
              transition={{ duration: 3, repeat: Infinity }}
            >
              🔄
            </FlowArrow>
            <FlowArrow
              animate={{ 
                x: [0, 10, 0],
                scale: [1, 1.2, 1]
              }}
              transition={{ duration: 2, repeat: Infinity, delay: 0.5 }}
            >
              ➡️
            </FlowArrow>
          </FlowArrows>

          <JobCategory type="created">
            <CategoryTitle type="created">📈 Jobs Being Created</CategoryTitle>
            <JobList>
              {currentData.created.map((job, index) => (
                <JobItem
                  key={job}
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ delay: 0.1 * index }}
                  whileHover={{ scale: 1.02 }}
                >
                  {job}
                </JobItem>
              ))}
            </JobList>
          </JobCategory>
        </JobFlowVisualization>
      </TimeMachineInterface>

      <TimelineContainer>
        <TimelineTitle>📊 Job Evolution Timeline</TimelineTitle>
        <TimelineTrack>
          <TimelineProgress
            initial={{ width: 0 }}
            animate={{ 
              width: `${((selectedYear - 2020) / (2035 - 2020)) * 100}%`
            }}
            transition={{ duration: 1, ease: "easeInOut" }}
          >
            {selectedYear} - {timePoints.find(p => p.year === selectedYear)?.label}
          </TimelineProgress>
        </TimelineTrack>
        
        <div style={{
          display: 'flex',
          justifyContent: 'space-between',
          fontSize: '0.9rem',
          color: '#64748b'
        }}>
          <span>2020</span>
          <span>2025</span>
          <span>2030</span>
          <span>2035</span>
        </div>
      </TimelineContainer>

      <div>
        <h3 style={{
          color: '#f1f5f9',
          textAlign: 'center',
          marginBottom: '2rem',
          fontSize: '1.5rem'
        }}>
          🎯 Future Scenarios - What Could Happen?
        </h3>
        
        <ScenarioCards>
          {scenarios.map((scenario, index) => (
            <ScenarioCard
              key={scenario.id}
              selected={selectedScenario === scenario.id}
              onClick={() => setSelectedScenario(
                selectedScenario === scenario.id ? null : scenario.id
              )}
              initial={{ opacity: 0, y: 50 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.1 * index }}
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
            >
              <ScenarioIcon>{scenario.icon}</ScenarioIcon>
              <ScenarioTitle>{scenario.title}</ScenarioTitle>
              <ScenarioDescription>{scenario.description}</ScenarioDescription>
            </ScenarioCard>
          ))}
        </ScenarioCards>
      </div>
    </SectionWrapper>
  );
};

export default JobDisplacement;